@mixin font-sets($ratio) {
  @for $i from 0 through 3 {
    @for $j from 0 through 9 {
      .font-#{$i}--#{$j}em {
        font-size: ($j * 0.1em + $i * 1em) * $ratio;
      }
    }
  }
}

@include font-sets(0.8);

@media only screen and (min-width: 600px) {
  @include font-sets(1);
}

@media only screen and (min-width: 1200px) {
  @include font-sets(1.3);
}
